<template>
    <div class="d-flex align-center justify-center pos-relative container" :style="{backgroundColor: COLOR_APP_BAR}">
        <div class="app-bar-content ab-left">
            <v-btn :class="{alarm: $root.$route.name === 'home'}" icon @click="($root.$route.name === 'home') ? $root.$router.push('notification') : $root.$router.go(-1)">
                <svg style="width: 26px; height: 26px;" :style="{fill: COLOR_ICON}" v-if="$root.$route.name === 'home'"><use xlink:href="@/assets/icons.svg#bell"></use></svg>
                <svg style="width: 26px; height: 26px;" :style="{fill: COLOR_ICON}" v-else><use xlink:href="@/assets/icons.svg#chevronLeft"></use></svg>
            </v-btn>
        </div>
        <logo-app font-size="1.2rem" :color="COLOR_ICON"/>
        <div class="app-bar-content ab-Right">
            <v-btn icon @click="$root.$store.commit('toggleHamburgerMenu', true)">
                <svg style="width: 30px; height: 30px;" :style="{fill: COLOR_ICON}"><use xlink:href="@/assets/icons.svg#hamburgerMenu"></use></svg>
            </v-btn>
            <v-btn icon class="mr-1" @click="$root.$router.push('questions')" v-if="$root.$route.name !== 'home'">
                <svg style="width: 25px; height: 25px;" :style="{fill: COLOR_ICON}"><use xlink:href="@/assets/icons.svg#help"></use></svg>
            </v-btn>
        </div>
    </div>
</template>

<script>
    import LogoApp from "./LogoApp";
    export default {
        name: "AppBar",
        components: {LogoApp},
        data: () => ({
            COLOR_APP_BAR: null,
            COLOR_ICON: null
        }),
        watch: {
            $route (to) {
                this.doColor(to.path);
            }
        },
        created() {
            this.doColor(window.location.pathname);
        },
        methods: {
            doColor(pathname) {
                this.COLOR_APP_BAR = (pathname === "/") ? '#208a96' : '#FEFEFE';
                this.COLOR_ICON = (pathname === "/") ? '#FCF7F8' : '#087E8B';
            }
        }
    }
</script>

<style scoped>
    .container {
        top: 0;
        left: 0;
        right: 0;
        height: 60px;
        z-index: 10;
        position: fixed;
    }
    .app-bar-content {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        position: absolute;
    }
    .ab-Right {
        right: 10px;
    }
    .ab-left {
        left: 10px;
    }
    .alarm::after {
        content: '';
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #C72828;
        top: 9px;
        right: 9px;
        position: absolute;
        box-shadow: 0 0 0 0 rgba(199, 40, 40, 1);
        animation: pulse 2s infinite;
    }
    @keyframes pulse {
        0% {
            box-shadow: 0 0 0 0 rgba(199, 40, 40, .8);
        }
        70% {
            box-shadow: 0 0 0 13px rgba(199, 40, 40, 0);
        }
        100% {
            box-shadow: 0 0 0 0 rgba(199, 40, 40, 0);
        }
    }
</style>