<template>
  <v-navigation-drawer
      v-model="$root.$store.state.isHamburgerMenu"
      temporary
      fixed
      right
      touchless
      floating
      width="230"
      color="#FEFEFE"
      class="container"
      @input="(!$event) ? $root.$store.commit('toggleHamburgerMenu', false) : null"
  >
    <v-card flat>
      <div class="d-flex align-center justify-center mt-5 mb-3">
        <logo-app font-size="1.05rem" color="#087E8B"/>
      </div>
      <div class="d-flex flex-column">
        <router-link class="d-flex align-center item-menu" :class="{active: $root.$route.path === item.to}" :to="item.to" v-for="(item, i) in items" :key="i">
          <div class="d-flex">
            <svg style="fill: #087E8B" :style="{width: item.icon.width, height: item.icon.height}"><use :xlink:href="require(`@/assets/icons.svg`) + `#${item.icon.uri}`"></use></svg>
          </div>
          <span class="menu-title" v-text="item.title"></span>
        </router-link>
      </div>
    </v-card>
  </v-navigation-drawer>
</template>

<script>
    import LogoApp from "./LogoApp";
    export default {
        name: "HamburgerMenu",
        components: {LogoApp},
        data: () => ({
            items: [
                {
                    to: '/history/demands',
                    title: 'درخواست',
                    icon: {uri: 'demands', width: '18px', height: '18px'}
                },
                {
                    to: '/history/transaction',
                    title: 'سابقه تراکنش',
                    icon: {uri: 'transaction', width: '21px', height: '17px'}
                },
                {
                    to: '/questions',
                    title: 'سوالات متداول',
                    icon: {uri: 'questions', width: '18px', height: '18px'}
                },
                {
                    to: '/profile',
                    title: 'حساب کاربری',
                    icon: {uri: 'profile', width: '18px', height: '18px'}
                },
                {
                    to: '/about',
                    title: 'درباره ما',
                    icon: {uri: 'about', width: '19px', height: '19px'}
                }
            ]
        })
    }
</script>

<style lang="scss" scoped>
  .v-navigation-drawer {
    transition-duration: .3s;
    transition-timing-function: cubic-bezier(0, .43, .36, 1);
  }
</style>

<style scoped>
  .container {
    z-index: 100;
    border-bottom-left-radius: 24px;
    border-top-left-radius: 24px
  }
  .item-menu {
    border-radius: 12px;
    padding: 12px;
    transition: all .2s ease-in-out;
  }
  .item-menu.active {
    background-color: rgba(217,217,217,.4);
  }
  .item-menu.active .menu-title {
    color: #027e96;
  }
  .item-menu:hover {
    background-color: rgba(217,217,217,.6);
  }
  .menu-title {
    font-weight: 500;
    font-size: .76rem;
    color: #000000;
    margin-right: 12px;
  }
</style>